.boss-battle {
  padding: 20px;
  background: linear-gradient(135deg, #0f0c29, #302b63, #24243e);
  min-height: 100vh;
  color: #ffffff;
  position: relative;
  overflow: hidden;
}

/* Add animated particles in the background */
.boss-battle::before {
  content: '';
  position: fixed;
  top: -50%;
  left: 0;
  right: 0;
  bottom: -50%;
  height: 200vh;
  background-image: 
    radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 40px),
    radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 30px),
    radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 40px);
  background-size: 
    550px 550px,
    350px 350px,
    250px 250px;
  background-position: 0 0, 0 0, 0 0;
  animation: particles 15s linear infinite;
  pointer-events: none;
  will-change: transform;
  transform: translateZ(0);
}

@keyframes particles {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(50%);
  }
}

/* Add a second layer for seamless transition */
.boss-battle::after {
  content: '';
  position: fixed;
  top: -50%;
  left: 0;
  right: 0;
  bottom: -50%;
  height: 200vh;
  background-image: 
    radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 40px),
    radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 30px),
    radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 40px);
  background-size: 
    550px 550px,
    350px 350px,
    250px 250px;
  background-position: 275px 275px, 175px 175px, 125px 125px;
  animation: particles 15s linear infinite;
  animation-delay: -7.5s;
  pointer-events: none;
  will-change: transform;
  transform: translateZ(0);
}

.round-counter {
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  color: #ffd700;
  padding: 15px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  margin-bottom: 20px;
  border: 2px solid rgba(255, 215, 0, 0.3);
  box-shadow: 0 0 15px rgba(255, 215, 0, 0.2);
  text-shadow: 0 0 10px rgba(255, 215, 0, 0.5);
}

/* Remove old end-phase-button styles */
.end-phase-button {
  display: none;
}

/* Add new round box styles */
.round-box {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.8);
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-top: none; /* Remove top border */
  border-radius: 0 0 15px 15px; /* Round only bottom corners */
  padding: 8px 15px; /* Reduced padding */
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  backdrop-filter: blur(5px);
  box-shadow: 
    0 4px 15px rgba(0, 0, 0, 0.4),
    0 0 30px rgba(255, 215, 0, 0.1);
  z-index: 100;
  width: 120px; /* Fixed width */
}

.round-number {
  color: #ffd700;
  font-size: 1rem; /* Smaller font */
  font-weight: bold;
  text-shadow: 0 0 10px rgba(255, 215, 0, 0.3);
}

.end-turn-button {
  background: linear-gradient(45deg, #ff4757, #ff6b81);
  color: white;
  border: none;
  padding: 4px 12px; /* Smaller padding */
  border-radius: 12px;
  font-weight: bold;
  font-size: 0.7rem; /* Smaller font */
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  box-shadow: 0 0 15px rgba(255, 71, 87, 0.3);
  width: 80px; /* Fixed width */
}

.end-turn-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 25px rgba(255, 71, 87, 0.5);
}

.end-turn-button:active {
  transform: translateY(0);
}

/* Remove the old status panel styles */
.status-panel {
  display: none;
}

/* General HP Bar Styles */
.hp-bar, .vertical-hp-bar {
  position: relative;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 
    inset 0 2px 8px rgba(0, 0, 0, 0.5),
    0 2px 4px rgba(0, 0, 0, 0.3);
}

.hp-bar {
  width: 100%;
  height: 15px;
  margin-bottom: 10px; /* Add some space below the HP bar */
}

.vertical-hp-bar {
  width: 12px; /* 20% smaller than the original 15px */
  height: calc(100% - 40px); /* Adjust height to match the field's height */
  position: absolute;
  z-index: 0; /* Lower z-index to position behind character images */
  top: 20px; /* Align with the top padding of the field */
}

.hp-fill, .vertical-hp-fill {
  background: linear-gradient(
    90deg,
    rgba(255, 71, 87, 1) 0%,
    rgba(255, 99, 132, 1) 50%,
    rgba(255, 159, 64, 1) 100%
  );
  transition: width 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 
    2px 0 10px rgba(255, 71, 87, 0.6),
    inset 0 2px 3px rgba(255, 255, 255, 0.3),
    inset 0 -2px 3px rgba(0, 0, 0, 0.2);
}

.hp-fill {
  height: 100%;
}

.vertical-hp-fill {
  width: 100%;
  position: absolute;
  bottom: 0;
}

.hp-bar span, .vertical-hp-text {
  position: absolute;
  width: 100%;
  text-align: center;
  line-height: 15px; /* Match the height of the HP bar */
  color: white;
  font-weight: bold;
  z-index: 1;
  font-size: 0.8rem;
}

/* Positioning for Vertical HP Bars */
.boss-field .vertical-hp-bar {
  left: 5px; /* Adjust position inside the boss field */
}

.player-field .vertical-hp-bar {
  right: 5px; /* Adjust position inside the player field */
}

/* Add glow effect to low HP */
.vertical-hp-fill.low-hp {
  animation: lowHpPulse 1.5s infinite;
  box-shadow: 
    0 0 15px rgba(255, 71, 87, 0.8),
    inset 0 2px 3px rgba(255, 255, 255, 0.3),
    inset 0 -2px 3px rgba(0, 0, 0, 0.2);
}

@keyframes lowHpPulse {
  0% { filter: brightness(1); }
  50% { filter: brightness(1.5); }
  100% { filter: brightness(1); }
}

/* Update game area layout */
.game-area {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin: 25px 0;
  perspective: 1000px;
  position: relative;
}

.boss-field, .player-field {
  width: 48%;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  transform: perspective(1000px) rotateX(5deg);
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  transition: transform 0.3s ease;
  position: relative;
  min-height: 400px; /* Set minimum height for the fields */
}

/* Position HP and mana bars */
.player-vertical-hp {
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
}

.boss-vertical-hp {
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
}

.field-mana-bar {
  position: absolute;
  bottom: 10px;
  left: 20px;
  right: 20px;
  height: 15px;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 
    inset 0 2px 8px rgba(0, 0, 0, 0.5),
    0 2px 4px rgba(0, 0, 0, 0.3);
  z-index: 0;
}

.field-mana-fill {
  height: 100%;
  background: linear-gradient(
    90deg,
    rgba(33, 150, 243, 1) 0%,
    rgba(30, 136, 229, 1) 50%,
    rgba(25, 118, 210, 1) 100%
  );
  transition: width 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 
    2px 0 10px rgba(33, 150, 243, 0.6),
    inset 0 2px 3px rgba(255, 255, 255, 0.3),
    inset 0 -2px 3px rgba(0, 0, 0, 0.2);
  z-index: 0;
}

/* Update HP text indicators */
.vertical-hp-text {
  position: absolute;
  left: 50%;
  top: 80%;
  transform: translate(-50%, -50%) rotate(-90deg);
  white-space: nowrap;
  color: white;
  font-weight: bold;
  z-index: 1;
  font-size: 0.7rem;
  padding: 1px 3px;
  border-radius: 3px;
}

/* Update mana text positioning */
.field-mana-text {
  position: absolute;
  width: 100%;
  text-align: center;
  line-height: 15px; /* Match the height of mana bar */
  color: white;
  font-weight: bold;
  z-index: 1;
  font-size: 0.8rem;
  right: 10%;
  top: 0;
}

/* Remove the separate mana number since we're showing it on the bar */
.mana-number {
  display: none;
}

.field-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 10px;
  padding: 10px;
  width: calc(100% - 40px); /* Adjust space for thinner HP bar */
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

/* Enhanced Ability Button Styles */
.player-controls {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 100;
}

.ability-button {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: linear-gradient(135deg, #00c6ff, #0072ff);
  border: 3px solid rgba(255, 255, 255, 0.3);
  color: white;
  font-weight: bold;
  font-size: 1.4rem;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: 
    0 0 20px rgba(0, 198, 255, 0.4),
    0 0 40px rgba(0, 114, 255, 0.2),
    inset 0 0 15px rgba(255, 255, 255, 0.2);
}

.ability-button::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255,255,255,0.3) 0%, transparent 60%);
  transform: rotate(45deg);
  transition: all 0.3s ease;
}

.ability-button:hover:not(:disabled) {
  transform: scale(1.1) translateY(-5px);
  box-shadow: 
    0 0 30px rgba(0, 198, 255, 0.6),
    0 0 50px rgba(0, 114, 255, 0.3),
    inset 0 0 20px rgba(255, 255, 255, 0.3);
}

.ability-button:hover:not(:disabled)::before {
  animation: ability-shine 1.5s infinite;
}

.ability-button:disabled {
  background: linear-gradient(135deg, #4a4a4a, #2d2d2d);
  border-color: rgba(255, 255, 255, 0.1);
  box-shadow: 
    0 0 10px rgba(0, 0, 0, 0.3),
    inset 0 0 5px rgba(0, 0, 0, 0.2);
}

/* Cooldown display */
.ability-button[disabled]::after {
  content: attr(data-cooldown);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.6rem;
  color: rgba(255, 255, 255, 0.7);
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

@keyframes ability-shine {
  0% {
    transform: rotate(45deg) translateY(0);
    opacity: 0.5;
  }
  50% {
    transform: rotate(45deg) translateY(-10%);
    opacity: 0.8;
  }
  100% {
    transform: rotate(45deg) translateY(0);
    opacity: 0.5;
  }
}

/* Ability ready pulse effect */
.ability-button:not(:disabled) {
  animation: ability-ready-pulse 2s infinite;
}

@keyframes ability-ready-pulse {
  0% {
    box-shadow: 
      0 0 20px rgba(0, 198, 255, 0.4),
      0 0 40px rgba(0, 114, 255, 0.2),
      inset 0 0 15px rgba(255, 255, 255, 0.2);
  }
  50% {
    box-shadow: 
      0 0 30px rgba(0, 198, 255, 0.6),
      0 0 60px rgba(0, 114, 255, 0.3),
      inset 0 0 25px rgba(255, 255, 255, 0.3);
  }
  100% {
    box-shadow: 
      0 0 20px rgba(0, 198, 255, 0.4),
      0 0 40px rgba(0, 114, 255, 0.2),
      inset 0 0 15px rgba(255, 255, 255, 0.2);
  }
}

.player-hand, .boss-hand {
  display: flex;
  gap: 2px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 20px;
  margin-top: 10px;
  overflow-x: auto;
  overflow-y: hidden; /* Prevent vertical scroll */
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  height: 145px;
  align-items: center; /* Center cards vertically */
  flex-wrap: nowrap; /* Prevent wrapping to new line */
}

.card-container {
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.card-container:hover:not(.disabled) {
  transform: translateY(-10px) scale(1.05);
  z-index: 1;
  transform-origin: center bottom; /* Scale from bottom center */
}

.card-container.disabled {
  opacity: 0.5;
  filter: grayscale(0.8);
}

.phase-indicator {
  display: none;
}

.log {
  display: none;
}

.controls, .log-message {
  display: none;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: rgba(255, 215, 0, 0.3);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 215, 0, 0.5);
}

/* Add animations for card effects */
@keyframes cardGlow {
  0% { box-shadow: 0 0 5px rgba(255, 215, 0, 0.5); }
  50% { box-shadow: 0 0 20px rgba(255, 215, 0, 0.8); }
  100% { box-shadow: 0 0 5px rgba(255, 215, 0, 0.5); }
}

@keyframes damageShake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  75% { transform: translateX(5px); }
}

/* Add these classes to apply animations when needed */
.card-glow {
  animation: cardGlow 1.5s infinite;
}

.damage-shake {
  animation: damageShake 0.5s ease-in-out;
}

/* Add smooth scrolling for hand */
.player-hand, .boss-hand {
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.player-hand::-webkit-scrollbar, 
.boss-hand::-webkit-scrollbar {
  display: none;
}

/* Add some professional touches */
h3, h4 {
  color: #ffd700;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  margin: 5px 0;
}

button {
  cursor: pointer;
  transition: transform 0.2s ease;
}

button:hover:not(:disabled) {
  transform: translateY(-2px);
}

button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}

/* Update mana number display */
.mana-number {
  position: absolute;
  right: -25px;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(33, 150, 243, 0.3);
  border: 1px solid rgba(33, 150, 243, 0.5);
  color: white;
  font-weight: bold;
  padding: 2px 5px;
  border-radius: 4px;
  font-size: 0.8rem;
  box-shadow: 0 0 10px rgba(33, 150, 243, 0.3);
}

/* Base background gradients for different elements */
.boss-battle.fire-element {
  background: linear-gradient(135deg, #1a0f0f, #2c1810, #1a0f0f);
}

.boss-battle.water-element {
  background: linear-gradient(135deg, #0f1a1a, #102c2c, #0f1a1a);
}

.boss-battle.air-element {
  background: linear-gradient(135deg, #1a1a1f, #28284d, #1a1a1f);
}

.boss-battle.earth-element {
  background: linear-gradient(135deg, #1a1510, #2c2810, #1a1510);
}

.boss-battle.light-element {
  background: linear-gradient(135deg, #1a1a15, #2c2c20, #1a1a15);
}

.boss-battle.dark-element {
  background: linear-gradient(135deg, #0f0c29, #302b63, #24243e);
}

/* Particle variations for different elements */
.boss-battle.fire-element::before,
.boss-battle.fire-element::after {
  background-image: 
    radial-gradient(#ff6b6b, rgba(255,107,107,.2) 2px, transparent 40px),
    radial-gradient(#ffd166, rgba(255,209,102,.15) 1px, transparent 30px),
    radial-gradient(#ff9f43, rgba(255,159,67,.1) 2px, transparent 40px);
  animation: particles-fire 20s linear infinite;
}

.boss-battle.water-element::before,
.boss-battle.water-element::after {
  background-image: 
    radial-gradient(#4facfe, rgba(79,172,254,.2) 2px, transparent 40px),
    radial-gradient(#00f2fe, rgba(0,242,254,.15) 1px, transparent 30px),
    radial-gradient(#63b3ed, rgba(99,179,237,.1) 2px, transparent 40px);
  animation: particles-water 25s linear infinite;
}

.boss-battle.air-element::before,
.boss-battle.air-element::after {
  background-image: 
    radial-gradient(#e0e0e0, rgba(224,224,224,.2) 2px, transparent 40px),
    radial-gradient(#f5f5f5, rgba(245,245,245,.15) 1px, transparent 30px),
    radial-gradient(#d1d1e1, rgba(209,209,225,.1) 2px, transparent 40px);
  animation: particles-air 15s linear infinite;
}

.boss-battle.earth-element::before,
.boss-battle.earth-element::after {
  background-image: 
    radial-gradient(#8d6e63, rgba(141,110,99,.2) 2px, transparent 40px),
    radial-gradient(#a1887f, rgba(161,136,127,.15) 1px, transparent 30px),
    radial-gradient(#bcaaa4, rgba(188,170,164,.1) 2px, transparent 40px);
  animation: particles-earth 30s linear infinite;
}

/* Custom animations for each element */
@keyframes particles-fire {
  0% {
    transform: translateY(0) rotate(0deg);
  }
  100% {
    transform: translateY(50%) rotate(45deg);
  }
}

@keyframes particles-water {
  0% {
    transform: translateY(0) rotate(0deg);
  }
  100% {
    transform: translateY(50%) rotate(-45deg);
  }
}

@keyframes particles-air {
  0% {
    transform: translateY(0) translateX(0);
  }
  50% {
    transform: translateY(25%) translateX(5%);
  }
  100% {
    transform: translateY(50%) translateX(0);
  }
}

@keyframes particles-earth {
  0% {
    transform: translateY(0);
    opacity: 0.8;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    transform: translateY(50%);
    opacity: 0.8;
  }
}

/* Particle sizes and densities */
.boss-battle::before,
.boss-battle::after {
  background-size: var(--particle-size, 550px 550px);
  opacity: var(--particle-opacity, 1);
}

/* Element-specific particle sizes */
.boss-battle.fire-element::before,
.boss-battle.fire-element::after {
  --particle-size: 350px 350px;
  --particle-opacity: 0.8;
}

.boss-battle.water-element::before,
.boss-battle.water-element::after {
  --particle-size: 450px 450px;
  --particle-opacity: 0.6;
}

.boss-battle.air-element::before,
.boss-battle.air-element::after {
  --particle-size: 650px 650px;
  --particle-opacity: 0.4;
}

.boss-battle.earth-element::before,
.boss-battle.earth-element::after {
  --particle-size: 250px 250px;
  --particle-opacity: 1;
}

/* Ensure card containers maintain consistent size */
.player-hand .card-container, 
.boss-hand .card-container {
  flex: 0 0 auto; /* Prevent shrinking and growing */
  height: 100%; /* Fill height of hand */
  display: flex;
  align-items: center;
}

/* Add these new animation styles */
@keyframes attackToRight {
  0% {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
  50% {
    transform: translateX(300px) rotate(15deg);
    opacity: 0.8;
  }
  80% {
    transform: translateX(0) rotate(0deg);
    opacity: 0.6;
  }
  100% {
    transform: translateX(0) rotate(0deg);
    opacity: 0;
  }
}

@keyframes attackToLeft {
  0% {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
  50% {
    transform: translateX(-300px) rotate(-15deg);
    opacity: 0.8;
  }
  80% {
    transform: translateX(0) rotate(0deg);
    opacity: 0.6;
  }
  100% {
    transform: translateX(0) rotate(0deg);
    opacity: 0;
  }
}

@keyframes defenseImpact {
  0% {
    transform: scale(1);
    filter: brightness(1);
  }
  50% {
    transform: scale(1.2);
    filter: brightness(1.5);
  }
  100% {
    transform: scale(1);
    filter: brightness(1);
  }
}

@keyframes damageNumber {
  0% {
    transform: translateY(0);
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  100% {
    transform: translateY(-50px);
    opacity: 0;
  }
}

/* Add classes for the animations */
.card-attack-right {
  animation: attackToRight 1s ease-in-out forwards;
}

.card-attack-left {
  animation: attackToLeft 1s ease-in-out forwards;
}

.card-defense-impact {
  animation: defenseImpact 0.5s ease-in-out;
}

/* Damage number display */
.damage-number {
  position: absolute;
  color: #ff4757;
  font-weight: bold;
  font-size: 1.5rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  z-index: 100;
  pointer-events: none;
  animation: damageNumber 1s ease-out forwards;
}

/* Add flash effect for receiving damage */
@keyframes damageFlash {
  0% {
    box-shadow: 0 0 0 rgba(255, 71, 87, 0);
  }
  50% {
    box-shadow: 0 0 30px rgba(255, 71, 87, 0.8);
  }
  100% {
    box-shadow: 0 0 0 rgba(255, 71, 87, 0);
  }
}

.field-damage-flash {
  animation: damageFlash 0.5s ease-in-out;
}

/* Character Container Styles */
.character-container {
  position: absolute;
  width: 100%;
  height: auto;
  bottom: 0;
  transition: all 0.3s ease;
  z-index: 1;
  overflow: hidden;
}

.boss-character {
  left: 0;
  transform: scaleX(-1); /* Face towards player */
}

.player-character {
  right: 0;
}

.boss-character img,
.player-character img {
  max-width: 100%;
  height: auto;
  display: block;
  position: relative;
  z-index: 1; /* Ensure images are above HP bars */
  transform: scale(0.8); /* Scale down to 80% of the original size */
}

/* Character States */
.character-state {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  transition: opacity 0.3s ease;
}

/* Idle Animation */
@keyframes idle {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

.character-idle {
  animation: idle 2s ease-in-out infinite;
}

/* Damaged Animation */
@keyframes damaged {
  0% { transform: translateX(0) rotate(0deg); filter: brightness(1); }
  25% { transform: translateX(-10px) rotate(-5deg); filter: brightness(1.5); }
  50% { transform: translateX(8px) rotate(3deg); filter: brightness(1.2); }
  75% { transform: translateX(-5px) rotate(-2deg); filter: brightness(1.3); }
  100% { transform: translateX(0) rotate(0deg); filter: brightness(1); }
}

.character-damaged {
  animation: damaged 0.5s ease-in-out;
}

/* Victory Animation */
@keyframes victory {
  0% { transform: translateY(0) scale(1); }
  50% { transform: translateY(-20px) scale(1.1); }
  100% { transform: translateY(0) scale(1); }
}

.character-victory {
  animation: victory 1s ease-in-out infinite;
}

/* Defeated Animation */
@keyframes defeated {
  0% { transform: translateY(0) rotate(0deg); opacity: 1; }
  100% { transform: translateY(20px) rotate(90deg); opacity: 0; }
}

.character-defeated {
  animation: defeated 1s ease-in-out forwards;
}

/* Emote System */
.emote-bubble {
  position: absolute;
  top: -60px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 255, 255, 0.9);
  padding: 10px;
  border-radius: 15px;
  font-size: 0.9rem;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  white-space: nowrap;
}

.emote-bubble::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid rgba(255, 255, 255, 0.9);
}

.emote-active {
  opacity: 1;
  animation: emoteFloat 2s ease-in-out;
}

@keyframes emoteFloat {
  0% { transform: translateX(-50%) translateY(10px); opacity: 0; }
  20% { transform: translateX(-50%) translateY(0); opacity: 1; }
  80% { transform: translateX(-50%) translateY(0); opacity: 1; }
  100% { transform: translateX(-50%) translateY(-10px); opacity: 0; }
}

/* Taunt Animation */
@keyframes taunt {
  0% { transform: scale(1) rotate(0deg); }
  25% { transform: scale(1.1) rotate(-5deg); }
  75% { transform: scale(1.1) rotate(5deg); }
  100% { transform: scale(1) rotate(0deg); }
}

.character-taunt {
  animation: taunt 0.8s ease-in-out;
}

/* Low HP State */
@keyframes lowHp {
  0% { transform: translateY(0) rotate(0deg); }
  25% { transform: translateY(2px) rotate(1deg); }
  75% { transform: translateY(-2px) rotate(-1deg); }
  100% { transform: translateY(0) rotate(0deg); }
}

.character-low-hp {
  animation: lowHp 1s ease-in-out infinite;
  filter: brightness(0.8);
}

/* Idle Animation */
@keyframes idleAnimation {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

.idle-animation {
  animation: idleAnimation 2s ease-in-out infinite;
}

.hp-bar {
  position: relative;
  width: 100%;
  height: 15px;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 
    inset 0 2px 8px rgba(0, 0, 0, 0.5),
    0 2px 4px rgba(0, 0, 0, 0.3);
  margin-bottom: 10px; /* Add some space below the HP bar */
}

.hp-fill {
  height: 100%;
  background: linear-gradient(
    90deg,
    rgba(255, 71, 87, 1) 0%,
    rgba(255, 99, 132, 1) 50%,
    rgba(255, 159, 64, 1) 100%
  );
  transition: width 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 
    2px 0 10px rgba(255, 71, 87, 0.6),
    inset 0 2px 3px rgba(255, 255, 255, 0.3),
    inset 0 -2px 3px rgba(0, 0, 0, 0.2);
}

.hp-bar span {
  position: absolute;
  width: 100%;
  text-align: center;
  line-height: 15px; /* Match the height of the HP bar */
  color: white;
  font-weight: bold;
  z-index: 1;
  font-size: 0.8rem;
}


